@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: none;
}

body {
    font-family: 'Merriweather', serif;
    background: #fdfdfd;
}

.formWrappers {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.headingTop {
    width: 100%;
    padding: 20px;
    background: #000;
    color: #fff;
    text-align: center;
    margin-bottom: 10px;
}

.inputFormCard {
    max-width: 500px;
    width: 100%;
    background: #fff;
    box-shadow: 6px 6px 10px 0px #00000012;
    border-radius: 10px;
    padding: 30px;
}

.inputFormCard form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.inputFormCard .formField {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 10px;
    width: 48%;
}

.inputFormCard .formField label {
    color: #000;
    font-size: 14px;
    letter-spacing: 0.5px;
    margin-bottom: 5px;
}

.inputFormCard .formField input {
    width: 100%;
    height: 30px;
    font-size: 18px;
    padding: 10px;
    color: #333;
    border: 0;
    letter-spacing: 0.5px;
    box-shadow: 2px 2px 10px 0 #00000029;
}

.inputFormCard button {
    height: 40px;
    width: 100%;
    border: 0;
    background: #000;
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    font-size: 18px;
    cursor: pointer;
}


.listunstyle {
    list-style: none;
    padding: 20px
}

.listunstyle li {
    max-width: 500px;
    width: 100%;
    margin: 0 auto 20px auto;
    box-shadow: 6px 6px 10px 0px #00000012;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    position: relative;
}


.listunstyle li .w100 {
    width: 100%;
    margin-bottom: 10px;
}

.listunstyle li .w50 {
    width: 50%;
    margin-bottom: 10px;
}

.listunstyle li .crossBtn {
    position: absolute;
    color: #F44336;
    padding: 5px;
    border-radius: 50%;
    font-size: 14px;
    font-weight: 900;
    right: 5px;
    top: 5px;
    cursor: pointer;
}

.listunstyle li .editBtn {
    padding: 5px;
    background: #000;
    color: #fff;
    font-size: 12px;
    cursor: pointer;
    margin-bottom: 0;
}




